.story {
    @extend %section-mrg-bottom;

    &__content {
        display: flex;
        flex-direction: column;
        gap: 50px;

        @media (max-width: 768px) {
            gap: 20px;
        }
    }
}

.ticket {
    @extend %main-border-radius;

    cursor: pointer;
    
    position: relative;
    
    max-width: 1170px;
    height: 567px;
    
    background: linear-gradient(270deg,
            rgba(0, 0, 0, 0) 0.01%,
            rgba(0, 0, 0, 0.2) 100%);

    overflow: hidden;

    @media (max-width: 768px) {
        height: 455px;
    }

    & img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;

        transition: transform 300ms ease;
    }

    @media(any-hover: hover) {
        &:hover img {
            transform: scale(1.2);
        }
    }

    &__body {
        color: $white;

        height: 100%;
        padding: 60px;

        display: flex;
        flex-direction: column;
        justify-content: space-between;

        @media (max-width: 768px) {
            padding: 30px;
        }
    }

    &__top {
        max-width: 554px;
    }

    &__tittle {
        margin-bottom: 37px;

        @media (max-width: 768px) {
            margin-bottom: 20px;
        }
    }

    &__text {
        & > * {
            margin-bottom: 28px;

            @media (max-width: 768px) {
                margin-bottom: 23px;
            }
        }
    }

    &__list {
        padding-left: 30px;

        @media (max-width: 768px) {
            padding-left: 24px;
        }

        & li::marker {
            font-size: 16px;

            @media (max-width: 768px) {
                font-size: 13px;
            }
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
    }

    &__social {
        display: flex;
        gap: 36px;

        padding: 0;
        list-style: none;

        @media (max-width: 576px) {
            display: none;
        }
    }

    &__social-link {

        text-decoration: none;
        transition: color 0.3s ease;

        @media(any-hover: hover) {
            &:hover {
                text-decoration-line: underline;
                text-underline-offset: 6px;
                text-decoration-thickness: 1px;
            }

            &:active {
                text-decoration-line: underline;
                text-underline-offset: 6px;
                text-decoration-thickness: 1px;

                color: $orange200;
            }
        }
    }

}